// change big image event

const currentBigPic = document.querySelector('[data-current-big]');
const imageSection = document.querySelector('.main__section--images');

imageSection.addEventListener('click', onThumbClick);

function onThumbClick(e) {
    let target = e.target.closest('picture');
    if (!target) return;

    for (let child of target.children) {
        console.log(child);
        child.focus();
    }

    let targSrcset = target.firstElementChild.getAttribute('srcset').replace(/-thumbnail/, '');
    let targSrc = target.lastElementChild.getAttribute('src').replace(/-thumbnail/, '');

    currentBigPic.firstElementChild.setAttribute('srcset', targSrcset);
    currentBigPic.lastElementChild.setAttribute('src', targSrc);
}

